import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { ToastController, ModalController } from '@ionic/angular';
import { LoadingController } from '@ionic/angular';
import { UserService } from '../services/user.service';
import { NewPinPage } from '../modal/new-pin/new-pin.page';
import { OverlayEventDetail } from '@ionic/core';
import { CommonService } from '../services/common.service';

@Component({
  selector: 'app-password',
  templateUrl: './password.page.html',
  styleUrls: ['./password.page.scss'],
})
export class PasswordPage implements OnInit {
  user: any = {
    email: '',
    password: ''
  };
  loading: any = null;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private modalController: ModalController,
    private commonService: CommonService,
    public toastController: ToastController,
    private userService: UserService,
    public loadingController: LoadingController
  ) { }

  ngOnInit() {
    this.user.email = this.route.snapshot.paramMap.get('email');
  }

  eventHandler(event: any) {
    if (event.keyCode === 13 && this.user.password) {
      this.login();
    }
  }
  async openPinModal() {
    const modal: HTMLIonModalElement =
       await this.modalController.create({
          component: NewPinPage,
          backdropDismiss: false,
          componentProps: {
             aParameter: true,
             otherParameter: new Date()
          }
    });
     
    modal.onDidDismiss().then((detail: OverlayEventDetail) => {
      if (detail) {
        this.commonService.presentLoading();
        this.userService.updatePin(detail.data)
          .subscribe((data: any) => {
            this.commonService.loadingBox.dismiss();
            this.router.navigate(['/tabs/tab1']);
            this.userService.setUserToLocal(data);
            this.commonService.presentToastWithOptions('Pin successfully added.');
          });

      }
    });
    
    await modal.present();
}

login() {
    this.commonService.presentLoading();
    this.userService.login(this.user)
    .subscribe((res: any) => {
      this.commonService.loadingBox.dismiss();
      if (res.error) {
        this.commonService.presentToastWithOptions(res.error);
      } else {
        this.userService.setLoginToLocal(res);
        if(res.user.mobile_pin) {
          this.router.navigate(['/tabs/tab1']);
        } else {
          this.openPinModal();
        }
      }
    });
  }

}
